function switchToAuth() {
    registration('none')
    auth('block')
}
function swithToReg() {
    auth('none')
    registration('block')
}


document.querySelector('.heart__btn').addEventListener('click', () => {
    if(document.cookie.includes('auth')) {
        window.open('account.php', '_blank');
    } else {
        auth('block')
    }
})
